<template>
  <div id="app">
    <section class="location-select">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <div class="row">
              <div class="col-12 col-sm-6">
                <h3>Выбор страны</h3>
                <div class="input-group mb-3">
                  <select class="custom-select" v-model="currentCountry" @change="countryChange">
                    <option value="RU">Russia</option>
                    <option value="UA">Ukraine</option>
                    <option value="BY">Belarus</option>
                  </select>

                </div>
              </div>
              <div class="col col-sm-6">
                <h3>Выбор города</h3>
                <div class="input-group mb-3">
                  <select class="custom-select" id="inputGroupSelect03" v-model="selectedCity" @change="fetchWeather">
                    <option :value="item.id" v-for="(item, ind) in cities" :key="item.id">{{item.name}}</option>
                  </select>
                </div>
              </div>
            </div>
            
            
          </div>
        </div>
      </div>
      
    </section>
    <section class="big-weather">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <div class="result-preloader" v-if="resultPreloader">
              <img src="assets/img/preloader.gif" alt="">
            </div>
            <div v-else>
                <div class="today-forecast">
                  <div class="today-forecast__left">
                    <div class="today-forecast__header">
                      <div class="main-city-name">Прогоз погоды в <strong>{{getCurrentCityWeather.name}},</strong> {{countries[currentCountry]}}</div>
                      <div>сегодня {{currentCityHourlyWeather.current.dt | myDateFilter}}</div>
                    </div>
                    <div class="today-temp">
                      <div class="today-temp__graph">
                        <img :src="getIconPath" alt="">
                        <span class="main-temp" v-html="$options.filters.celciumPlus(getCurrentCityWeather.main.temp)"></span>
                        <!-- {{getCurrentCityWeather.main.temp | celcium}} -->
                      </div>
                      <!-- <h2>{{getCurrentCityWeather.name}}</h2> -->
                      <div>{{getCurrentCityWeather.weather[0].description}}
                        <div>
                          <strong>
                            ощущается как <span v-html="$options.filters.celcium(getCurrentCityWeather.main.feels_like)"></span>
                            
                          </strong>
                        </div>

                      </div>
                      <!-- <pre>{{getCurrentCityWeather}}</pre> -->
                    </div>
                    
                  </div>
                  <div class="today-forecast__right">
                    <div class="today-forecast__col">
                      <h5>Утром</h5>
                      <div>
                        <span v-html="$options.filters.celcium(currentCityHourlyWeather.daily[0].temp.morn)"></span>
                      </div>
                    </div>
                    <div class="today-forecast__col">
                      <h5>Днем</h5>
                      <div>
                        <span v-html="$options.filters.celcium(currentCityHourlyWeather.daily[0].temp.day)"></span>
                      </div>
                    </div>
                    <div class="today-forecast__col">
                      <h5>Вечером</h5>
                      <div>
                        <span v-html="$options.filters.celcium(currentCityHourlyWeather.daily[0].temp.eve)"></span>
                      </div>
                    </div>
                    <div class="today-forecast__col">
                      <h5>Ночью</h5>
                      <div>
                        <span v-html="$options.filters.celcium(currentCityHourlyWeather.daily[0].temp.night)"></span>
                      </div>
                    </div>
                  </div>
                  <!-- <pre>{{currentCityHourlyWeather}}</pre> -->
                </div>
                
              <div class="week-forecast">
                <div class="week-forecast__card"  v-for="(day, index) in currentCityHourlyWeather.daily" :key="day.dt">
                  <p class="text-center" v-if="index > 0">{{day.dt | myDateFilterWeek}}</p>
                  <p v-else>Сегодня</p>
                  <p class="week-forecast__icon"><img :src="getWeeklyIconPath(day.weather[0].icon)" alt="alt"></p>
                  <p class="text-center day-night"><span v-html="$options.filters.celciumPlus(day.temp.day)"></span><span v-html="$options.filters.celciumPlus(day.temp.night)" style="opacity: .5"></span></p>
                  <div class="d-none d-xl-block">
                    <p class="text-center daily-descr">{{day.weather[0].description}}</p>
                    <p>давление: {{day.pressure | pressure}}</p>
                    <p>влажность: {{day.humidity}}%</p>
                    <p>ветер: {{day.wind_speed}}м/с</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
    </section>
    <div class="result-preloader" v-show="resultPreloader">
      <img src="img/preloader.gif" alt="">
    </div>
   <!--  <section v-show="!resultPreloader" class="chart">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <h2 class="text-center">Почасовой прогноз на 48 часов</h2>
          </div>
        </div>
      </div>
      <div class="container">
        <div class="row">
          <div class="col-12">
            <div class="chart-wrap" v-show="!resultPreloader">
              <div class="chart-wrap__inner">
                <canvas ref="canvas" id="myChart" width="400" height="400"></canvas>
              </div>
            </div>
          </div>
        </div>
      </div>
      
    </section> -->
  </div>
</template>

<script>
// import {Line} from 'vue-chartjs'

export default {
  name: 'App',
  data(){
    return{
      cityData: [],
    selectedCity: null,
    currentCountry: 'RU',
    countries: {
      RU: "Russia",
      BY: "Belarus",
      UA: "Ukrain"
    },
    // currentCityWeather: {},
    currentCityWeather: {
      "coord": {
        "lon": "",
        "lat": ""
      },
      "weather": [
      {
        "id": "",
        "main": "",
        "description": "",
        "icon": ""
      }
      ],
      "base": "",
      "main": {
        "temp": "",
        "feels_like": "",
        "temp_min": "",
        "temp_max": "",
        "pressure": "",
        "humidity": ""
      },
      "visibility": "",
      "wind": {
        "speed": "",
        "deg": ""
      },
      "clouds": {
        "all": ""
      },
      "dt": "",
      "sys": {
        "type": "",
        "id": "",
        "country": "",
        "sunrise": "",
        "sunset": ""
      },
      "timezone": "",
      "id": "",
      "name": "",
      "cod": ""
    },
    currentCityHourlyWeather: {
     
      current: {
        dt: ''
      },
      hourly: [],
      daily: [
        {
           weather: [
             {
              icon: ''
             }
          ],
          temp:{
            eve: '',
            night: '',
            morn: '',
            night: ''

          }
        }
      ]
    },
    resultPreloader: false,
    queryPreloader: false,
    myChart: null ,
    fetchWeatherCounter: 0,
    inputedCity: ''
    }
  },
  computed: {
    cities(){
      return this.cityData.filter(item => item.country === this.currentCountry && item.name !== '-')
        .sort((a, b) => {
          if(a.name.toLowerCase() > b.name.toLowerCase()){
            return 1
          }
          if(a.name.toLowerCase() < b.name.toLowerCase()){
            return -1
          }
          return 0
        })
    },
    getCityName(){
      if(this.selectedCity){
        return this.cities.filter(item => item.id === this.cities[0]['id'])[0]['name']
      }
      return ''
    },
    getCurrentCityWeather(){
      return this.currentCityWeather
    },
    getIconPath(){
      return 'https://openweathermap.org/img/wn/'+this.getCurrentCityWeather.weather[0].icon+'@2x.png' 
    },
    getChartData(){
      const labels = this.currentCityHourlyWeather.hourly.map(key => this.$options.filters.dateConvert(key.dt) + 'ч')
      const dataChart = this.currentCityHourlyWeather.hourly.map(key => Math.round(key.temp - 273))
      return {labels, dataChart}
    }
  },
  methods: {
    findCity(){
      console.log(this.inputedCity)
      this.selectedCity = this.cities.filter(city => city.name.toLowerCase() === this.inputedCity.toLowerCase())[0].id
      this.fetchWeather();

    },
    async fetchWeather(){
      this.resultPreloader = true
      const weather = await fetch('https://api.openweathermap.org/data/2.5/weather?id='+this.selectedCity+'&appid=34e679245e0fc389640636a857efea87&lang=RU')
      const data = await weather.json()
      this.currentCityWeather = data
      this.resultPreloader = false

      this.fetchWeatherHourly()
    },
    async fetchWeatherHourly(){
      this.resultPreloader = true

      const weather = await fetch('https://api.openweathermap.org/data/2.5/onecall?lat='+this.getCurrentCityWeather.coord.lat+'&lon='+this.getCurrentCityWeather.coord.lon+'&exclude=minutely,alerts&appid=34e679245e0fc389640636a857efea87&lang=RU')
      const data = await weather.json()
      this.currentCityHourlyWeather = data

      if(this.fetchWeatherCounter > 0){
        // this.chartUpdate()
      }else{
        console.log('else!')
        // this.chartSetup()
        // this.setupSelect()
        // this.swiperSetup()
      }
      this.fetchWeatherCounter++
      this.resultPreloader = false
    },
    async fetchWeatherHourlyOnce(){
      // this.resultPreloader = true

      const weather = await fetch('https://api.openweathermap.org/data/2.5/onecall?lat='+this.getCurrentCityWeather.coord.lat+'&lon='+this.getCurrentCityWeather.coord.lon+'&exclude=minutely&appid=34e679245e0fc389640636a857efea87&lang=RU')
      const data = await weather.json()
      this.currentCityHourlyWeather = data

      // this.chartSetup() 
      // this.resultPreloader = false
    },
    countryChange(){
      this.selectedCity = this.cities[0].id
      this.fetchWeather()
    },
    chartUpdate(){
      this.myChart.data.datasets[0].data = this.getChartData.dataChart;
      this.myChart.update();
    },
    chartSetup(){
      console.log(this.$refs.chart)
      Chart.plugins.register({ //регистрация плагина для постоянного отображения туллтипов на графике
        afterDatasetsDraw: function(chartInstance, easing) {
              // To only draw at the end of animation, check for easing === 1
              var ctx = chartInstance.chart.ctx;

              chartInstance.data.datasets.forEach(function (dataset, i) {
                var meta = chartInstance.getDatasetMeta(i);
                if (!meta.hidden) {
                  meta.data.forEach(function(element, index) {
                          // Draw the text in black, with the specified font
                          ctx.fillStyle = 'rgb(0, 0, 0)';

                          var fontSize = 12;
                          var fontStyle = 'normal';
                          var fontFamily = 'Helvetica Neue';
                          ctx.font = Chart.helpers.fontString(fontSize, fontStyle, fontFamily);

                          // Just naively convert to string for now
                          var dataString = "";
                          // if((index!=0)&&(index!=4)){
                            dataString = dataset.data[index].toString() + '\xB0' + 'C'; //значение, выводимое в  туллтип, с кодом символа градуса
                          // }


                          // Make sure alignment settings are correct
                          ctx.textAlign = 'center';
                          ctx.textBaseline = 'middle';

                          var padding = 20;
                          var position = element.tooltipPosition();
                          ctx.fillText(dataString, position.x, position.y - (fontSize / 2) - padding);
                      });
                }
              });
          }
      });
        var ctx = this.$refs.chart.getContext('2d')

        this.myChart = new Chart(ctx, {
          type: 'line',
          data: {
             labels: this.getChartData.labels,//названия точек
             datasets: [{
                  data: this.getChartData.dataChart, //значения
                  backgroundColor: 'rgba(255, 99, 132, 0.1)',
                  pointBorderColor: 'transparent',
                  pointBackgroundColor: 'transparent',
                  borderColor: '#EB6E4B',
                  fill: true,
                  borderWidth: 3
                }]
          },
          options: {
            tooltips: {
                mode: false //отключаем стандартное появление туллтипов по ховеру на точке
              },
            showAllTooltips: true,
            responsive: true,
              maintainAspectRatio: false, //для установки высоты кастомной всего графика
            legend: {
              display: false
            },
              scales: {
                  yAxes: [{
                      ticks: {
                          // beginAtZero: true,
                      display: false, //убираем отображение цифр на оси Y
                      // min: Math.min(...this.dataChart) - 3,
                      max: Math.max(...this.dataChart) + 3 //установка максимального значения по оси Y

                    },
                    gridLines: {
                      display: false, //убираем сетку с поля графика
                  }
                }],
                xAxes: [{
                  scaleLabel:{
                    display: true,
                    labelString: 'часы',
                  },
                    gridLines: {
                      display: false, //убираем сетку с поля графика
                      drawBorder: false
                    }
                }]
            }
        }

      });
    },
    getWeeklyIconPath(icon){
      return 'https://openweathermap.org/img/wn/'+icon+'@2x.png'
    },
    swiperSetup(){
      var mySwiper = new Swiper(this.$refs.swiper, {
        slidesPerView: 8
        // navigation: {
        //     nextEl: '.swiper-button-next',
        //     prevEl: '.swiper-button-prev',
        //   }
      })
    }
  },
  filters: {
    pressure(value){
      return Math.round(value * 0.00750063755419211 * 100) + 'мм.рт.ст.'
    },
    dateConvert(value){
      const hours = new Date(value *1000).getHours()
      if(hours < 10){
        return '0' + hours
      }else{
        return hours.toString()
      }
    },
    celcium(value){
      return `${Math.round(value - 273)}<span>&#176;C</span>`
    },
    celciumPlus(value){
      return `${Math.round(value - 273)}<span>&#176;</span>`
    },
    dateFilter(value){
      const options = {
        day: '2-digit',
        month: 'long',
        year: 'numeric',
        hour: '2-digit', 
        minute: '2-digit', 
        second: '2-digit'
      }
      const locale ='ru-RU'
      console.log('data gilter locale ' + locale)
      return new Intl.DateTimeFormat(locale, options).format(new Date(value))
    },
    myDateFilter(value){
      const date = new Date(value * 1000)
      const opts = {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'short'
      }
      return date.toLocaleString('ru', opts)
    },
    myDateFilterWeek(value){
      const date = new Date(value * 1000)
      const opts = {
        weekday: 'short',
        day: 'numeric',
        month: 'numeric',
      }
      return date.toLocaleString('ru', opts)
    }
  },
  mounted(){
    console.log('mounted')
    console.log(this.currentCityWeather)
      fetch('./done.json')
      .then(resp => resp.json())
      .then(data => {
        console.log(data)
        this.cityData = data.filter(key => key.country === 'RU' ||  key.country === 'UA' ||  key.country === 'BY')
        this.selectedCity = this.cities[0].id
        this.fetchWeather()
        // this.fetchWeatherHourlyOnce()
      })
      .catch(err => console.error(err))
      // this.cityData = this.cityList.filter(key => key.country === 'RU' ||  key.country === 'UA' ||  key.country === 'BY')
  }
}
</script>

